.home-container {
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 90vw;
  margin: auto;

}
.l-title {
  left: -100px;
  position: relative;
  width: 80px;
}
.video{
  width: 100%;
}
.h-leftdiv {
  width: 60vw;
  /* border: 1px solid red; */
text-align: start;
  color: var(--secondary-color);
}
.l-title {
    font-size: 30px;
    text-align: start;
    
  }
  .web-developer{
    position: relative;
    left: 100px;
  }
.h-left-name {
  /* border: 1px solid red; */
  width: 80%;
  top: 90px;
  left: 50px;
  position: relative;
  /* text-align: start; */
}

.my-name {
  color: var(--title-color);
  font-size: 65px;
  position: relative;
  left: 20px;
}

.cv-button{
    color: var(--secondary-color);
    top: 170px;
    position: relative;
    background-color:var(--title-color);
    border-radius: 20px;
    padding: 10px;
    left: 200px;
}
.video{
  position: absolute;
}


.h-rightdiv {
  width: 60vw;
  /* flex: 1; */
  /* border: 1px solid green; */
  position: relative;
  /* align-items: fl/ex-start; */
}

.hero-img {
  width: 25vw;
  height: 67vh;
  position: relative;
  left: -20px;
  top: 70px;
}

.vector-blue{
    position: relative;
}
@media screen and (max-width: 480px) {

    .home-container {
        display: flex;
        flex-direction: column;
            position: relative;
            width: 100vw;
            margin: auto;
     
      }

      .my-name {
        font-size: 40px;
        left: 10px;
      }
      .h-left-name {
        top: 20px;
        left: 20px;
      }
      .web-developer{
  
        left: 100px;
        font-size: 14px;
      }
      .hero-img {
        width: 54vw;
        height: 38vh;
        left: -20px;
        top:-80px;
      }
      .social-icons{

        top: 150px;
        left: 180px;
        width: 60%;
        }
        .social-icons>a>img{
            width: 30px;
        }
        .cv-button{
            top: 250px;
            padding: 5px;
            left: 250px;
            font-size: 14px;
        }
}